import React,{ Component } from 'react';
import { Link } from 'react-router-dom'; 
import axios from 'axios';
import { SketchPicker } from 'react-color';
import DefaultImage from '../resources/NoNoteImage.png';
import urlConst from '../config/config';
import Background from '../resources/dashboard_image.jpg';

export default class FormPage extends Component{    
    state={
        _id:'',
        title: '',
        body: '',
        noteColor: '#fff',  
        image: DefaultImage,
        notes : [],
        file:'',
        validation:{
            _id: null,
            title: null,
            body: null,
            noteColor: null,
            image: null
        }
    };
    componentDidMount() {
        let id=localStorage.getItem('note_edit_id');
        if(id!==''){
            axios.get(urlConst.url +`/notes/${id}`,
            // { headers: {'authorization': `${localStorage.token}`}}
            )
            .then(resp =>{     
                this.setState({
                    _id:resp.data[0]._id,
                    title: resp.data[0].title,
                    body: resp.data[0].body,
                    noteColor: resp.data[0].noteColor,
                    image: resp.data[0].image,
                });
            }); 
        }
    }   
   

  constructor(props : any){
    super(props);
    this.formInputChange= this.formInputChange.bind(this);
    this.formSubmit= this.formSubmit.bind(this);
  }   

 formInputChange(event : any){
    localStorage.setItem('note_edit_id', '');   
    this.setState({
        [event.target.name] : event.target.value
    });
    const validation: any = this.state.validation;
    if(!event.target.value){
        validation[event.target.name]='Can\'t be empty';
    }
    else{
        validation[event.target.name]=null;
    }
    this.setState({
        validation
    });
 }  

 handleChangeComplete = (color: any, event:any) => {
    this.setState({ noteColor: color.hex });
 };

 onImageChange = (event : any)=> {
    //console.log(event.target.files[0]);
    if (event.target.files && event.target.files[0]) {
      let img = event.target.files[0];
      this.setState({
          image: URL.createObjectURL(img),
          file:  img
      });
    }
  };

 formSubmit(event : any){
    event.preventDefault();
    const notes : any[]= this.state.notes;
    notes.push({
        _id: this.state._id,
        title: this.state.title,
        body: this.state.body,
        noteColor: this.state.noteColor,
        image: this.state.image,
    })
    this.setState({
        notes,
        _id:'',
        title: '',
        body: '',
        noteColor:'#fff',
        image: ''
    })

    const formData = new FormData();
    formData.append('file', this.state.file);
    formData.append('title', notes[0].title);
    formData.append('body', notes[0].body);
    formData.append('noteColor', notes[0].noteColor);

    if(notes[0]._id!==''){  // Edit Note
        axios.put(urlConst.url +`/notes/${notes[0]._id}`,formData,         
         { headers: {'authorization': `${localStorage.token}`}}
       )
       .then((response :any) => {
            alert("Note updated successfully");
            window.location.href = '/dashboard';
        }).catch((error: any) => {
        }); 
        
    }
    else{         // Add Note
        axios.post(urlConst.url +'/notes',formData,
        { headers: {'authorization': `${localStorage.token}`}},        
        )
        .then((response :any) => {
            alert("Note created successfully");
        }).catch((error: any) => {
        });  
    }    
 }
    render(){          
        return(
            <div style={{ backgroundColor: "black", backgroundImage: `url(${Background})`, height: "657px", backgroundSize: 'cover', backgroundAttachment:'fixed' }}>
            <div className='container col-4' >
                <br/>
                
                <form onSubmit={this.formSubmit}>
                    <div className='form-row'>
                        <input type='text' name='title' placeholder='Title' className='form-control' value={this.state.title} 
                        onChange={this.formInputChange}/>
                        {this.state.validation.title ? <span style={{color:'red'}}>{this.state.validation.title}</span> : null}
                    </div>
                    <br/>
                    <div className='form-row'>
                        <input type='text' name='body' placeholder='Body' className='form-control' value={this.state.body}
                        onChange={this.formInputChange}/>
                        {this.state.validation.body ? <span style={{color:'red'}}>{this.state.validation.body}</span> : null}
                    </div>
                    <br/>
                    <div className='form-row'>
                        <SketchPicker className='cover' color={ this.state.noteColor }  onChangeComplete={ this.handleChangeComplete  }/>   
                        {this.state.validation.noteColor ? <span style={{color:'red'}}>{this.state.validation.noteColor}</span> : null} 
                    </div>      
                    <br/>
                    <div className='form-row'>
                        <img src={this.state.image} style={{height:100, width:250, float:'left'}} />
                        <input type="file" name="myImage" style={{ width: 100, float: 'right', marginLeft: 30, marginTop: 70 }}  onChange={this.onImageChange} />                   
                    </div>
                  
                    <br/>
                    <div className='form-row'>
                        <button type='submit' className='btn btn-success'>Save Note</button>
                        <Link to="/dashboard" className='btn btn-primary ml-5'>Go to Dashboard </Link>
                    </div>
                </form>
            </div>
            </div>
        )
    }

}